'use client';
import { useEffect, useMemo, useRef, useState } from 'react';
import { useRouter, useSearchParams } from 'next/navigation';
import { useScanner } from '@/lib/useScanner';
import { progress, saveJob, getJob } from '@/lib/api'; // removed searchJobs

type CutsBlock = {
  'Hind - Steak'?: boolean; 'Hind - Roast'?: boolean; 'Hind - Grind'?: boolean; 'Hind - None'?: boolean;
  'Front - Steak'?: boolean; 'Front - Roast'?: boolean; 'Front - Grind'?: boolean; 'Front - None'?: boolean;
};
type Job = {
  tag?: string; status?: string; Paid?: boolean; paid?: boolean; processType?: string; sex?: string;
  hind?: CutsBlock; front?: CutsBlock; hindRoastCount?: string; frontRoastCount?: string;
  steak?: string; steaksPerPackage?: string; burgerSize?: string; beefFat?: boolean;
  backstrapPrep?: string; backstrapThickness?: string;
  specialtyProducts?: boolean; summerSausageLbs?: string|number; summerSausageCheeseLbs?: string|number; slicedJerkyLbs?: string|number;
  webbsOrder?: boolean; webbsFormNumber?: string; webbsPounds?: string;
  notes?: string; price?: number|string; customer?: string;
};

const moneyNumber = (v:any)=> {
  if (typeof v === 'string') { const n = Number(v.replace(/[^0-9.\-]/g,'')); return Number.isFinite(n)?n:NaN; }
  const n = Number(v); return Number.isFinite(n)?n:NaN;
};
const normProc = (s?:string) => {
  const v = String(s||'').toLowerCase();
  if (v.includes('cape') && !v.includes('skull')) return 'Caped';
  if (v.includes('skull')) return 'Skull-Cap';
  if (v.includes('euro')) return 'European';
  if (v.includes('standard')) return 'Standard Processing';
  return '';
};
const suggestedPrice = (proc?:string, beef?:boolean, webbs?:boolean) => {
  const p = normProc(proc);
  const base = p==='Caped' ? 150 : (['Standard Processing', 'Skull-Cap', 'European'].includes(p) ? 130 : 0);
  return base ? base + (beef?5:0) + (webbs?20:0) : 0;
};
const normalizedPrice = (job: Job) => {
  let n = moneyNumber(job.price);
  if (!Number.isFinite(n) || n<=0 || n>10000) n = suggestedPrice(job.processType, !!job.beefFat, !!job.webbsOrder);
  return Math.max(0, n||0);
};

export default function ButcherIntake() {
  const sp = useSearchParams();
  const router = useRouter();
  const tag = sp.get('tag') || '';
  const [job, setJob] = useState<Job>({ tag, status: 'Processing' });
  const [busy, setBusy] = useState(false);
  const [msg, setMsg] = useState('');
  const price = useMemo(() => normalizedPrice(job), [job]);

  // Load job
  useEffect(() => {
    (async () => {
      if (!tag) return;
      try{
        const res = await getJob(tag);
        if (res?.exists && res.job) setJob(res.job);
        else setMsg('Tag not found');
      }catch(e:any){ setMsg(e?.message || 'Load failed'); }
    })();
    // Fullscreen + wake lock
    (async () => {
      try { if (!document.fullscreenElement) await document.documentElement.requestFullscreen(); } catch {}
      // @ts-ignore
      try { if (navigator.wakeLock?.request) { /* @ts-ignore */ await navigator.wakeLock.request('screen'); } } catch {}
    })();
  }, [tag]);

  // Listen for second scan to finish
  useScanner(async (scanned) => {
    if (scanned !== tag) return; // only act on the same deer
    try {
      const data = await progress(scanned); // <<< changed: explicit helper
      if (data?.ok && data.nextStatus === 'Finished') {
        setMsg('Finished ✓');
        setTimeout(()=> router.replace('/scan'), 800);
      } else {
        setMsg(data?.error || 'Cannot finish');
        setTimeout(()=> setMsg(''), 1200);
      }
    } catch (e:any) {
      setMsg(e?.message || 'Finish failed');
      setTimeout(()=> setMsg(''), 1200);
    }
  });

  // Small setters
  const setVal = (k: keyof Job, v:any) => setJob(p=>({ ...p, [k]: v }));
  const toggle = (path: string) => {
    setJob(p => {
      const next = { ...p };
      if (path.startsWith('hind.') || path.startsWith('front.')) {
        const [block, key] = path.split('.') as ['hind'|'front', keyof NonNullable<CutsBlock>];
        next[block] = { ...(p[block]||{}), [key]: !p[block]?.[key] };
      } else if (path in p) {
        // @ts-ignore
        next[path] = !p[path];
        if (path === 'Paid') next.paid = !!next.Paid;
      }
      return next;
    });
  };

  const doSave = async () => {
    try{
      setBusy(true); setMsg('');
      const res = await saveJob(job);
      if (!res?.ok) throw new Error(res?.error || 'Save failed');
      setMsg('Saved ✓');
      setTimeout(()=> setMsg(''), 1000);
    }catch(e:any){ setMsg(e?.message || 'Save failed'); }
    finally{ setBusy(false); }
  };

  // Auto-fit to viewport (no scroll)
  const rootRef = useRef<HTMLDivElement|null>(null);
  useEffect(() => {
    const fit = () => {
      const el = rootRef.current; if (!el) return;
      el.style.transform = 'scale(1)';
      el.style.transformOrigin = 'top left';
      const rect = el.getBoundingClientRect();
      const scale = Math.min(window.innerWidth / rect.width, (window.innerHeight - 12) / rect.height, 1);
      el.style.transform = `scale(${scale})`;
    };
    fit();
    window.addEventListener('resize', fit);
    return () => window.removeEventListener('resize', fit);
  }, []);

  // Barcode render (compact)
  useEffect(() => {
    const render = () => {
      try {
        const code = job?.tag || '';
        const wraps = document.querySelectorAll<HTMLElement>('#barcodeWrap');
        if (!code) { wraps.forEach(w => w.style.display='none'); return; }
        // @ts-ignore
        const JsBarcode = (window as any).JsBarcode;
        if (!JsBarcode) return;
        document.querySelectorAll<SVGSVGElement>('svg#tagBarcode').forEach(svg => {
          JsBarcode(svg, code, {
            format:'CODE128', lineColor:'#111', width:1.25, height:18, displayValue:true,
            font:'monospace', fontSize:10, textMargin:2, margin:0
          });
        });
      } catch (e) {
        console.error('Barcode render error', e);
        document.querySelectorAll<HTMLElement>('#barcodeWrap').forEach(w => (w.style.display='none'));
      }
    };
    // lazy load if needed
    // @ts-ignore
    if ((window as any).JsBarcode) render();
    else {
      const s = document.createElement('script');
      s.src = 'https://cdn.jsdelivr.net/npm/jsbarcode@3.11.6/dist/JsBarcode.all.min.js';
      s.onload = render;
      s.onerror = () => document.querySelectorAll<HTMLElement>('#barcodeWrap').forEach(w => (w.style.display='none'));
      document.head.appendChild(s);
    }
  }, [job?.tag]);

  return (
    <main className="page-wrap butcher-mode">
      <div className="butcher-root" ref={rootRef}>
        <div className="toprow">
          <div className="tagbox">
            <div className="tag">{job.tag || '—'}</div>
            <div id="barcodeWrap"><svg id="tagBarcode" role="img" aria-label="Tag barcode"></svg></div>
          </div>
          <div className="statusbox">
            <div className="row"><span className="label">Status</span><span className="badge">{job.status || '—'}</span></div>
            <div className="row"><span className="label">Paid</span><button className={'pill ' + (job.Paid?'on':'')} onClick={()=>toggle('Paid')}>{job.Paid ? 'PAID' : 'UNPAID'}</button></div>
            <div className="row"><span className="label">Process</span><span className="val">{job.processType || '—'}</span></div>
            <div className="row price"><span className="label">Price</span><span className="money">${price.toFixed(2)}</span></div>
          </div>
          <div className="who">
            <div className="name">{job.customer || '—'}</div>
            <div className="notes" title={job.notes||''}>{(job.notes||'').slice(0,140)}</div>
          </div>
        </div>

        <div className="grid4">
          <section className="card">
            <h3>Hind Quarter</h3>
            <div className="checks">
              <button className={'chk ' + (job.hind?.['Hind - Steak']?'on':'')} onClick={()=>toggle('hind.Hind - Steak')}>Steak</button>
              <button className={'chk ' + (job.hind?.['Hind - Roast']?'on':'')} onClick={()=>toggle('hind.Hind - Roast')}>Roast</button>
              <div className="count"><span>Count</span><input value={job.hindRoastCount||''} onChange={(e)=>setVal('hindRoastCount', e.target.value)} /></div>
              <button className={'chk ' + (job.hind?.['Hind - Grind']?'on':'')} onClick={()=>toggle('hind.Hind - Grind')}>Grind</button>
              <button className={'chk ' + (job.hind?.['Hind - None']?'on':'')} onClick={()=>toggle('hind.Hind - None')}>None</button>
            </div>
          </section>

          <section className="card">
            <h3>Front Shoulder</h3>
            <div className="checks">
              <button className={'chk ' + (job.front?.['Front - Steak']?'on':'')} onClick={()=>toggle('front.Front - Steak')}>Steak</button>
              <button className={'chk ' + (job.front?.['Front - Roast']?'on':'')} onClick={()=>toggle('front.Front - Roast')}>Roast</button>
              <div className="count"><span>Count</span><input value={job.frontRoastCount||''} onChange={(e)=>setVal('frontRoastCount', e.target.value)} /></div>
              <button className={'chk ' + (job.front?.['Front - Grind']?'on':'')} onClick={()=>toggle('front.Front - Grind')}>Grind</button>
              <button className={'chk ' + (job.front?.['Front - None']?'on':'')} onClick={()=>toggle('front.Front - None')}>None</button>
            </div>
          </section>

          <section className="card">
            <h3>Packaging</h3>
            <div className="kv"><span>Steak</span><span className="val">{job.steak || '—'}</span></div>
            <div className="kv"><span>Per Pkg</span><span className="val">{job.steaksPerPackage || '—'}</span></div>
            <div className="kv"><span>Burger</span><span className="val">{job.burgerSize || '—'}</span></div>
            <div className="kv"><span>Beef Fat</span><button className={'pill ' + (job.beefFat?'on':'')} onClick={()=>toggle('beefFat')}>{job.beefFat?'Yes':'No'}</button></div>
          </section>

          <section className="card">
            <h3>Backstrap</h3>
            <div className="kv"><span>Prep</span><span className="val">{job.backstrapPrep || '—'}</span></div>
            <div className="kv"><span>Thickness</span><span className="val">{job.backstrapThickness || '—'}</span></div>
          </section>

          <section className="card">
            <h3>Specialty</h3>
            <div className="kv"><span>Want</span><button className={'pill ' + (job.specialtyProducts?'on':'')} onClick={()=>toggle('specialtyProducts')}>{job.specialtyProducts?'Yes':'No'}</button></div>
            <div className="kv"><span>Summer</span><span className="val">{job.summerSausageLbs || '0'} lb</span></div>
            <div className="kv"><span>+Cheese</span><span className="val">{job.summerSausageCheeseLbs || '0'} lb</span></div>
            <div className="kv"><span>Jerky</span><span className="val">{job.slicedJerkyLbs || '0'} lb</span></div>
          </section>

          <section className="card">
            <h3>Webbs</h3>
            <div className="kv"><span>Order</span><button className={'pill ' + (job.webbsOrder?'on':'')} onClick={()=>toggle('webbsOrder')}>{job.webbsOrder?'Yes':'No'}</button></div>
            <div className="kv"><span>Form #</span><span className="val">{job.webbsFormNumber || '—'}</span></div>
            <div className="kv"><span>Pounds</span><span className="val">{job.webbsPounds || '—'}</span></div>
          </section>
        </div>

        <div className="actions">
          <div className={'status ' + (msg.includes('✓')?'ok': (msg? 'err':''))}>{msg}</div>
          <button className="btn" disabled={busy} onClick={()=>router.replace('/scan')}>Back to Scan</button>
          <button className="btn primary" disabled={busy} onClick={doSave}>{busy ? 'Saving…' : 'Save'}</button>
        </div>
      </div>

      <style jsx>{`
        .butcher-mode .butcher-root{
          width: 1280px; /* designed for 720p and scales */
          padding: 8px 10px 12px;
          transform-origin: top left;
        }
        .toprow{ display:grid; grid-template-columns: 340px 360px 1fr; gap:10px; margin-bottom:10px; }
        .tagbox{ background:#fff; border:1px solid var(--border); border-radius:12px; padding:10px; }
        .tag{ font-size:28px; font-weight:900; letter-spacing:1px; }
        #barcodeWrap{ margin-top:6px; }
        .statusbox{ background:#fff; border:1px solid var(--border); border-radius:12px; padding:10px; display:grid; gap:6px; }
        .row{ display:flex; justify-content:space-between; align-items:center; gap:12px; }
        .row .label{ font-size:12px; color:#475569; font-weight:700; }
        .row .val{ font-weight:800; }
        .row.price .money{ font-weight:900; font-size:20px; }
        .badge{ font-weight:800; font-size:12px; padding:2px 8px; border-radius:999px; border:1px solid var(--border); }
        .pill{ padding:6px 10px; border-radius:999px; border:1px solid var(--border); font-weight:800; background:#fff; }
        .pill.on{ background:#ecfdf5; border-color:#10b981; color:#065f46; }
        .who{ background:#fff; border:1px solid var(--border); border-radius:12px; padding:10px; overflow:hidden; }
        .who .name{ font-weight:900; font-size:16px; }
        .who .notes{ color:#475569; font-size:12px; white-space:nowrap; overflow:hidden; text-overflow:ellipsis; }

        .grid4{ display:grid; grid-template-columns: repeat(4, 1fr); gap:10px; }
        .card{ background:#fff; border:1px solid var(--border); border-radius:12px; padding:10px; }
        .card h3{ margin:0 0 6px; font-size:14px; }
        .checks{ display:flex; gap:8px; flex-wrap:wrap; }
        .chk{ padding:8px 10px; border:1px solid var(--border); border-radius:10px; background:#fbfdff; font-weight:800; }
        .chk.on{ background:#edf6ff; border-color:#93c5fd; }
        .count{ display:flex; align-items:center; gap:8px; margin:6px 0; }
        .count input{ width:60px; text-align:center; padding:6px 8px; border:1px solid var(--border); border-radius:8px; background:#fff; }

        .kv{ display:flex; justify-content:space-between; gap:8px; padding:4px 0; }
        .kv .val{ font-weight:800; }

        .actions{ display:flex; align-items:center; gap:8px; margin-top:10px; }
        .status{ min-width:120px; font-size:12px; color:#334155; margin-right:auto; }
        .status.ok{ color:#065f46; } .status.err{ color:#b91c1c; }
        .btn{ padding:8px 12px; border:1px solid var(--border); border-radius:8px; background:#155acb; color:#fff; font-weight:800; }
        .btn.primary{ background:#155acb; }
      `}</style>
    </main>
  );
}

